.home-cateory {
  background: #ffffff;
  margin-bottom: 0.4rem;
}

.carousel-item ul {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: row wrap;
  flex-flow: row wrap;
  -ms-flex-pack: center;
  justify-content: center;
  list-style: none;
  padding-top: 0.586667rem;
}

.carousel-item ul li {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-flow: column wrap;
  flex-flow: column wrap;
  -ms-flex-align: center;
  align-items: center;
  width: 1.84rem;
  height: 1.973333rem;
}

.carousel-item ul li span {
  text-align: center;
  color: #323232;
  font-size: 0.32rem;
}

.carousel-item ul li i {
  display: block;
  width: 1.2rem;
  height: 1.2rem;
  border-radius: 50%;
  background-repeat: no-repeat;
  background-size: 1.2rem auto;
  background-position: top center;
  background-color: #eee;
  margin-bottom: 0.133333rem;
}

.carousel-index {
  text-align: center;
  padding-bottom: 0.32rem;
}

.carousel-index span {
  display: inline-block;
  margin: 0 0.133333rem;
  width: 0.213333rem;
  height: 0.213333rem;
  border-radius: 50%;
  background-color: #eee;
}

.carousel-index span.active {
  background-color: rgb(233, 32, 61);
}

