.city-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  align-items: center;
  height: 1.333333rem;
  background-color: #fff;
  /*rgb(233, 32, 61);*/
  /*color: orange;*/
}

.city-header span {
  position: relative;
  top: 0.053333rem;
  margin-left: 0.613333rem;
  font-size: 0.533333rem;
  color: rgb(255, 102, 51);
}

.city-header div {
  margin-left: 2.133333rem;
  color: #fff;
}

.city-header div button {
  border: none;
  background-color: #fff;
  border: 1px solid rgb(255, 102, 51);
  width: 2.133333rem;
  height: 0.8rem;
  font-size: 0.4rem;
  color: rgb(255, 102, 51);
}

.hairlines .city-header div button {
  border: 0.5px solid rgb(255, 102, 51);
}

.city-header div button:focus {
  outline: none;
}

.city-header div button.active {
  background-color: rgb(255, 102, 51);
  color: #fff;
}

.city-header div button:first-child {
  border-radius: 0.16rem 0 0 0.16rem;
}

.city-header div button:last-child {
  border-radius: 0 0.16rem 0.16rem 0;
}


